.library-body{
  width: 94%;
  height:94%;
  padding: 3px;
  margin-left: 30px;
  display: flex;
  flex-wrap: wrap;
  overflow-y: auto;
  justify-content: space-between;
}
.playlist-card{
  position: relative;
  cursor: pointer;
  width: 13rem;
  height: 250px;
  border-radius: 20px;
  background-color: rgb(30, 42, 62);
  /* border: 1px solid white; */
  box-shadow: 10px;
  padding: 1%;
  margin-bottom: 2%;
  margin-top: 20px;
  background: linear-gradient(75deg, rgb(40, 58, 88) 0%, rgba(54, 69, 98, 0)100%);
}
.playlist-card:hover{
 transform: scale(1.02);
 transition: all 0.2s ease;
}
.playlist-card:hover .playlist-fade{
opacity: 1;
}
.playlist-image{
  width: 100%;
  border-radius:15px ;
  aspect-ratio: 1;
}
.playlist-title{
  font-weight: 800;
  font-size: 16px;
  color: aliceblue;
  margin: 10px 0px 0px;
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient:vertical ;
}
.playlist-subtitle{
  font-weight: 400;
  font-size: 12px;
  margin: 10px 0px;
  color: azure;
}
.playlist-fade{
  position: absolute;
  right: 0;
  bottom: 0;
  opacity: 0;
  width: 84%;
  height: 34%;
  border-radius: 20px;
  display: flex;
  align-items: flex-end;
  justify-content:flex-end ;
  padding: 8%;
  transition: 0.5s ease;
}
.previewimage{
  width:100px;
  margin-top: 20px;
}
 .play-btn {
  all: unset;
  cursor: pointer;
}